import { useEffect, useState } from "react";
import { useRouter } from "next/router"; // Mudar para next/router
import type { NextPage } from "next"; // Adicionar tipo para NextPage

const AnimeDetail: NextPage = () => {
  const router = useRouter();
  // No Pages Router, router.query pode estar vazio inicialmente
  const { id } = router.query;

  const [anime, setAnime] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [tab, setTab] = useState<"characters" | "staff" | "videos">("characters");
  const [data, setData] = useState<any[]>([]);
  const [tabLoading, setTabLoading] = useState(false); // Estado separado para loading da tab

  useEffect(() => {
    async function loadAnime() {
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}`);
        const json = await res.json();
        setAnime(json.data);
      } catch {
        setError(true);
      } finally {
        setLoading(false);
      }
    }

    if (id) {
      loadAnime();
    }
  }, [id]);

  useEffect(() => {
    async function loadTab() {
      if (!anime || !id) return;
      setTabLoading(true);
      try {
        let url = "";
        if (tab === "characters") url = `https://api.jikan.moe/v4/anime/${id}/characters`;
        if (tab === "staff") url = `https://api.jikan.moe/v4/anime/${id}/staff`;
        if (tab === "videos") url = `https://api.jikan.moe/v4/anime/${id}/videos`;

        const res = await fetch(url);
        const json = await res.json();
        setData(json.data || []);
      } catch {
        setError(true);
      } finally {
        setTabLoading(false);
      }
    }
    loadTab();
  }, [tab, anime, id]);

  // Adicione estas verificações para debugging
  console.log("ID:", id);
  console.log("Router:", router);

  if (loading) return <p style={{ color: "white", textAlign: "center" }}>Carregando anime...</p>;
  if (error || !anime) return <p style={{ color: "white", textAlign: "center" }}>Erro ao carregar anime.</p>;

  return (
    <div
      style={{
        width: "100%",
        minHeight: "100vh",
        backgroundImage: "url('/scenario.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        padding: 0,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        margin: 0,
        position: "relative", // Adicionar para posicionamento absoluto dos filhos
      }}
    >
      {/* MOVER O TÍTULO PARA BAIXO PARA OS BOTÕES FICAREM VISÍVEIS */}
      <div style={{ marginTop: "100px" }}></div>
      
      <h1 style={{ color: "white", marginBottom: 20 }}>{anime.title}</h1>

      {/* BOTÕES - REMOVER position: absolute OU ajustar */}
      <div
        style={{
          display: "flex",
          gap: 20,
          marginBottom: 30, // Adicionar margem
        }}
      >
        {["characters", "staff", "videos"].map((tabName) => (
          <button
            key={tabName}
            onClick={() => setTab(tabName as any)}
            style={{
              padding: "10px 20px",
              borderRadius: "8px",
              backgroundColor: tab === tabName ? "#1e88e5" : "#333",
              color: "white",
              fontWeight: "bold",
              cursor: "pointer",
              border: "none",
              boxShadow: "0 2px 5px rgba(0, 0, 0, 0.3)",
              transition: "background-color 0.3s, transform 0.2s",
            }}
            onMouseEnter={(e) => {
              const target = e.currentTarget;
              target.style.transform = "scale(1.05)";
            }}
            onMouseLeave={(e) => {
              const target = e.currentTarget;
              target.style.transform = "scale(1)";
            }}
          >
            {tabName === "characters" ? "Personagens" : tabName === "staff" ? "Staff" : "Vídeos"}
          </button>
        ))}
      </div>

      <div style={{ display: "flex", gap: 20, marginBottom: 20 }}>
        <img
          src={anime.images?.jpg?.image_url}
          alt={anime.title}
          width={150}
          height={200}
          style={{ objectFit: "cover", borderRadius: 6 }}
        />
        <div style={{ color: "white" }}>
          <p>Ano: {anime.year || "Desconhecido"}</p>
          <p>Score: {anime.score || "N/A"}</p>
          <p>Status: {anime.status || "N/A"}</p>
        </div>
      </div>

      <div style={{ display: "flex", flexDirection: "column", gap: 10, marginTop: 20 }}>
        {tabLoading && <p style={{ color: "white" }}>Carregando {tab}...</p>}
        {!tabLoading && data.length === 0 && <p style={{ color: "white" }}>Nenhum dado nesta aba.</p>}
        {!tabLoading &&
          data.length > 0 &&
          data.map((item: any, idx: number) => (
            <div key={idx} style={{ display: "flex", gap: 10, alignItems: "center" }}>
              {tab === "characters" && (
                <>
                  <img
                    src={item.character?.images?.jpg?.image_url}
                    alt={item.character?.name}
                    width={50}
                    height={70}
                    style={{ objectFit: "cover", borderRadius: 6 }}
                  />
                  <div style={{ color: "white" }}>
                    <p>{item.character?.name}</p>
                    <p>{item.role}</p>
                  </div>
                </>
              )}
              {tab === "staff" && (
                <div style={{ color: "white" }}>
                  <p>Nome: {item.person?.name}</p>
                  <p>Posições: {item.positions?.join(", ")}</p>
                </div>
              )}
              {tab === "videos" && (
                <div style={{ color: "white" }}>
                  <p>{item.title}</p>
                  <a href={item.url} target="_blank" rel="noopener noreferrer" style={{ color: "#1e88e5" }}>
                    Assistir
                  </a>
                </div>
              )}
            </div>
          ))}
      </div>
    </div>
  );
};

export default AnimeDetail;